import React, { useEffect, useState } from 'react';
import {
  Link,
} from 'dva/router';
import styled, { keyframes } from "styled-components";
import BigNumber from 'bignumber.js'
import ellipsis from "../../assets/ellipsis.png";
import SettingsPopup from "./SettingsPopup";
import WalletButton from "./WalletButton";
import logo from "../../assets/GASDAOTRANSPARENT.png";
import Web3 from 'web3';
import useWeb3Modal from './hooks/useWeb3Modal';
const lpABI = require("../../abi/lp.json");
const pool2Abi = require("../../abi/pool.json");
const routerAbi = require("../../abi/unirouter.json");

const HeaderContainer = styled.div`
  width: 100vw;
  align-items: stretch;
`;

const HeaderOptions = styled.div`
  padding: 2rem 1rem;
  min-height: 2.75rem;
  display: flex;
  align-items: center;
  @media only screen and (min-width: 728px) {
    padding: 2rem 4rem;
  }
`;

const LogoContainer = styled.div`
  display: flex;
  flex: 1;
  justify-content: flex-start;
`;

const SettingsContainer = styled.div`
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
`;

const wiggle = keyframes`
  from {transform:rotate(0deg);}
  to {transform:rotate(10deg);}
`;

const reverse = keyframes`
  from {transform:rotate(10deg);}
  to {transform:rotate(0deg);}
`;

const LogoIcon = styled.div`
  cursor: pointer;
  font-size: 40px;
  animation: ${reverse} 0.5s;
  color: #107cf1;
  &:hover {
    animation: ${wiggle} 0.5s;
    animation-fill-mode: forwards;
  }
`;

const Settings = styled.button`
  height: 38px;
  width: 42px;
  border-radius: 12px;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 2rem;
  border: 1px solid #fff;
  &:hover {
    border: 1px solid #c9c9c9;
  }
`;

const Ellipsis = styled.img`
  display: flex;
  height: 25px;
  width: 25px;
`;

export interface Props {
  provider: (() => Promise<void>) | undefined;
  loadWeb3Modal: (() => Promise<void>) | undefined;
  logoutOfWeb3Modal: (() => Promise<void>) | undefined;
  address: string;
  setIsClaiming: (isClaiming: boolean) => void;
}

function Header(props: Props) {
  const [apy, setApy] = useState(0);
  const [provider] = useWeb3Modal();

  
  const fetchApy = async () => {
    if (!provider || provider === undefined) {
      return;
    }
    // @ts-ignore
    const web3: any = new Web3(provider);
    const lpAddr = '0x4c053c1cd9047263e70c9f731691aead7a56be94';
    // const lpAddr = '0xc28c4e511db16a0b7510bc3140dded1426962156'; // goerli
    const poolAddr = '0x5251fF06Fc6EfA30AdA0e5560CA907AeE023Fb69';
    // const poolAddr = '0xeB3C085FBc1030bb127114CB1A3B9A02A24eF62d'; // goerli
    const uniswapRouterAddr = '0x7a250d5630B4cF539739dF2C5dAcb4c659F2488D';
    const DOSAddr = '0xc2acf7540f76a3cb389513b7128eb1e4d0d72391';
    const WETH = '0xc02aaa39b223fe8d0a0e5c4f27ead9083c756cc2';

    const lpContract = new web3.eth.Contract(
      lpABI.abi,
      lpAddr
    );

    const poolContract = new web3.eth.Contract(
      pool2Abi.abi,
      poolAddr
    );

    const routerContract = new web3.eth.Contract(
      routerAbi,
      uniswapRouterAddr
    );

    web3.eth.extend({
      methods: [
        {
          name: "chainId",
          call: "eth_chainId",
          outputFormatter: web3.utils.hexToNumber,
        },
      ],
    });

    // @ts-ignore
    const reserveData = await lpContract.methods
      .getReserves()
      .call();
    const { reserve0 } = reserveData;
    // @ts-ignore
    const totalSupply = await lpContract.methods
      .totalSupply()
      .call();

    const lpPrice = new BigNumber(reserve0).multipliedBy(2)
      .dividedBy(new BigNumber(totalSupply))
    // .dividedBy(1e18);
    // @ts-ignore
    const totalStake = await poolContract.methods
      .totalSupply()
      .call();
    // @ts-ignore
    let rewardRate = await poolContract.methods
      .rewardRate()
      .call();

    rewardRate = new BigNumber(rewardRate);

    // rewardRate = new BigNumber(100000000 / (7 * 24 * 60 * 60)).multipliedBy(1e18);

    // @ts-ignore
    let dosPrice = await routerContract.methods
      .getAmountsOut(new BigNumber(1e18), [DOSAddr, WETH])
      .call();

    dosPrice = dosPrice[dosPrice.length - 1];
    const tempApy = (((rewardRate)
      .multipliedBy(new BigNumber(dosPrice).multipliedBy(1e18 * 7 * 24 * 60 * 60)))
      .dividedBy(new BigNumber(totalStake).multipliedBy(new BigNumber(lpPrice))))
      .multipliedBy(52)
      .dividedBy(1e34);
    setApy(tempApy.toNumber().toFixed(2));
  }

  useEffect(() => {
    fetchApy();
  }, [provider])
  return (
    <HeaderContainer>
      <HeaderOptions>
        <LogoContainer>
          <Link to='/'>
            <LogoIcon onClick={() => props.setIsClaiming(false)}>
              {/* <FaGasPump /> */}
              <img
                style={{ height: "58px", width: "58px" }}
                src={logo}
                alt="settings"
              />
            </LogoIcon>
          </Link>
        </LogoContainer>
        <SettingsContainer>
          <Link to={'/farm'}>
            <span style={{ fontSize: 26, cursor: 'pointer' }}>apy {apy}%</span>
          </Link>
          <WalletButton
            provider={props.provider}
            loadWeb3Modal={props.loadWeb3Modal}
            logoutOfWeb3Modal={props.logoutOfWeb3Modal}
            address={props.address}
          />
          <SettingsPopup
            trigger={
              <Settings>
                <Ellipsis src={ellipsis} alt="settings" />
              </Settings>
            }
          />
        </SettingsContainer>
      </HeaderOptions>
    </HeaderContainer>
  );
}

export default Header;
